<template>
    <input
            type="text"
            ref = "input"
            v-bind:value="value"
            v-on:input="updateValue($event.target.value)"
    >
</template>

<script>
  export default {
    name: "doubleBinding",
    // 绑定从父组件prop传入的value
    props: ["value"],
    methods: {
      updateValue (value) {
        const formatValue = value.trim()
          .slice(0, value.indexOf(".") === -1
            ? value.length
            : value.indexOf(".") + 3);

        if (formatValue !== value) {
          this.$refs.input.value = formatValue;
        }

        // 触发父组件的input事件
        this.$emit("input", formatValue);
      }
    }

  };
</script>

<style scoped>

</style>



