<template>
    <div id = "component">
        local counter shows: {{count}}<br/>
        state begin:{{begin}}<br/>
        state counting: {{counting}}<br/>
        state terminate: {{terminate}}<br/>
        state getter: {{gcount}} {{gdoublecount}}<br/>
        props data: {{message}}
        <!--person appear: {{person.appear ? person.appear : "ugly"}}-->
        <button @click = "addProperty">component add</button>
        <button @click = "trigger">trigger action</button>
        <button @click = "changeName">changeInChild</button>
    </div>
</template>

<script>
  import * as mutations from "../vuex/mutation-types";
  import { mapActions, mapGetters, mapMutations, mapState } from "vuex";
  export default {
    props: ["message"],
    computed: {
      ...mapState("ma", [
        "count",
        "begin",
        "terminate",
        "counting",
        "person"
      ]),
      ...mapGetters("ma", [
        "gcount",
        "gdoublecount"
      ])
    },
    methods: {
      ...mapMutations("mb", { addProperty: mutations.GROWUP_MUTATION }),

      ...mapActions("mb", { trigger: "actionC" }),
      changeName () {
        this.$emit("changeName");
      }
    }
  };
</script>

<style scoped>

</style>



