<template>
    <div>
        <h3 class="title">
            test {{paraTitle}}
        </h3>
        <slot></slot>
        <hr/>
    </div>
</template>

<script>
  export default {
    name: "paragraphComponent",
    props: ["paraTitle"]
  };
</script>

<style scoped>
    h3.title{
        position: relative;
        margin: 10px 10px;
        padding: 1px;
    }
    h3.title:before{
        content: "";
        display: block;
        margin-top: -91px;
        height: 91px;
        visibility: hidden;
    }
    h3.title:after{
        content: "#";
        color: #42b983;
        position: absolute;
        left: -0.7em;
        bottom: -2px;
        font-size: 1.2em;
        font-weight: bold;
    }
</style>



