<template>
    <div>
        <ele-table
                v-bind:data = "tableData"
        >
            <ele-table-column
                    label="日期"
                    prop="date"
                    width="180"
            >
            </ele-table-column>
            <ele-table-column
                    label="姓名"
                    prop="name"
                    width="180"
            >
            </ele-table-column>
            <ele-table-column
                    label="地址"
                    prop="address"
            >
            </ele-table-column>
        </ele-table>
    </div>
</template>

<script>
  import { ElTable, ElTableColumn } from "element-table";

  const tableData = [
    {
      date: "2016-05-02",
      name: "王小虎",
      address: "上海市普陀区金沙江路 1518 弄"
    },
    {
      date: "2016-05-04",
      name: "王小虎",
      address: "上海市普陀区金沙江路 1517 弄"
    },
    {
      date: "2016-05-04",
      name: "王小虎",
      address: "上海市普陀区金沙江路 1517 弄"
    }
  ];

  export default {
    name: "tableTest",
    data () {
      return { tableData };
    },
    components: {
      "ele-table": ElTable,
      "ele-table-column": ElTableColumn
    }
  };
</script>

<style scoped>

</style>



