<template>
    <div>
        <transition name = "fade">
            <p v-show="show">
                hello miracle!
            </p>
        </transition>
        <button id="transButton" @click = "toggle">hello</button>
    </div>
</template>

<script>
  export default {
    data () {
      return { show: true };
    },
    methods: {
      toggle () {
        console.log("hello");
        this.show = !this.show;
      }
    }
  };
</script>

<style scoped>
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
</style>



